@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* about section  */
.about-sec {
  background-image: url(/static/media/about-bg.bef41720.jpg);
  height: 45vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
/* about us track  */
.about-us-title {
  background-color: white;
  text-transform: uppercase;
  font-size: 10px;
  color: #4c5e78;
  padding: 9px 20px;
  margin: 0px 0px 14px;
  border-radius: 20px;
  box-shadow: 5px 4px 13px 0px rgb(46 63 99 / 15%);
}
.about-us-title::before {
  content: "";
  display: inline-block;
  background: #66cfa9;
  width: 7px;
  height: 7px;
  border-radius: 7px;
  margin-right: 7px;
  margin-bottom: 2px;
  vertical-align: middle;
}
/* about us elemenetor-container */
.elemenetor-container {
  display: flex;
  align-items: center;
  text-align: start;
  margin-top: 20px;
  margin-left: 30px;
}
/* about us elemenetor-container image */
.elemenetor-container img {
  width: 80%;
  border-radius: 20px;
}
/* about us elemenetor-container image */
.elemenetor-container p {
  width: 80%;
  line-height: 26px;
}
/* about section contact us button  */
.about-more-info-btn {
  border: none;
  background-color: #4a81f8;
  color: white;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 5px 4px 13px 0 rgba(75, 131, 252);
}

.about-more-info-btn:hover {
  background-color: #66cfa9;
  transition: 0.5s;
  color: white;
}

.about-container {
  margin-bottom: 60px ;
}

/* contact us section  */
.contact-sec {
  background-image: url(/static/media/contact-1.bef41720.jpg);
  height: 45vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
/* contact container  */
.contact-container {
  display: flex;
  align-items: center;
  text-align: start;
  margin-top: 20px;
  margin-left: 30px;
}
/* contact container image  */
.contact-container img {
  width: 90%;
  border-radius: 20px;
}
/* contact container paragraph  */
.contact-container p {
  width: 80%;
  line-height: 26px;
}
.contact-us-title {
  background-color: white;
  text-transform: uppercase;
  font-size: 12px;
  color: #4c5e78;
  padding: 10px 30px;

  border-radius: 20px 20px 20px 20px;
  box-shadow: 5px 4px 13px 0px rgb(46 63 99 / 15%);
}
.contact-us-title::before {
  content: "";
  display: inline-block;
  background: #66cfa9;
  width: 7px;
  height: 7px;
  border-radius: 7px;
  margin-right: 7px;
  margin-bottom: 2px;
  vertical-align: middle;
}
/* contact us form  */
.contact-form {
  width: 90%;
}
/* get in touch button  */
.contact-btn {
  border: none;
  background-color: #4b83fc;
  box-shadow: 5px 4px 13px 0 rgba(75, 131, 252);
  color: white;
  padding: 15px 30px;
  border-radius: 5px;
  text-transform: uppercase;
}

.contact-btn:hover {
  background-color: #66cfa9;
  transition: 0.5s;
  color: white;
}

.contact-margin {
  margin-bottom: 60px;
}

div {
  padding: 0;
  margin: 0;
}
.footer {
  background-color: #172538;
  padding: 5px;
  margin-top: 16px;
}
.footer-content {
  display: flex;
  padding: 25px;
}
.medica-part {
  text-align: start;
  padding-left: 40px;
}

.medica-para {
  font-size: 14px;
  line-height: 2;
  font-weight: 900;
  padding-top: 60px;
  width: 80%;
}

.useful-links ul {
  list-style: none;
  margin-top: 70px;
}

.useful-links h6 {
  text-transform: uppercase;
  font-weight: 900;
  text-align: start;
  padding-left: 95px;
  padding-top: 10px;
}

.useful-links li {
  text-align: start;
  font-size: 14px;
  font-weight: 900;
  padding-left: 70px;
  padding-top: 5px;
  cursor: pointer;
}
.useful-links li:hover {
  text-decoration: underline;
}

.contact-us {
  text-align: start;
}

.contact-us h6 {
  text-transform: uppercase;
  font-weight: 900;
  text-align: start;
  padding-left: 65px;
  padding-top: 10px;
}
.contact-us .contact {
  font-size: 15px;
  padding-left: 70px;

  margin-top: 70px;
}

.time {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.location {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.message {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.footer-divider {
  color: #fff;
}
.footer-titlte {
  color: #fff;
  font-weight: 900;
}

@media only screen and (max-width: 576px) {
  .footer-content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .useful-links h6 {
    text-transform: uppercase;
    font-weight: 900;
    text-align: start;
    padding-left: 40px;
    padding-top: 40px;
    text-decoration: underline;
  }
  .useful-links li {
    text-align: start;
    font-size: 14px;
    font-weight: 900;
    padding-top: 5px;
    cursor: pointer;
    margin-top: 0px;
  }
}

.nav-bg {
  background-color: white;
  box-shadow: 1px 1px 10px rgb(212, 219, 217);
}

